.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.pr-section{
  background: rgba(3, 2, 68, 0.733);
  width: 100px;;
  height: 100px;;
  text-align: center;
  align-items: center;
  margin: auto;
  border-radius:50%;
  vertical-align: middle;
}

.pr-section h5{
  color:rgb(235, 233, 233);
  padding-top:30px;
  text-decoration: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
